<template> 
      <div class="waterfall">
        <!-- 使用计算属性来动态显示物品列表 -->
        <div v-for="(item, index) in displayedItems" :key="index" class="relative shop-item" >
          <div class="relative">
            <img :src="require('@/assets/shop/' + item.src)" :alt="item.alt" class="image" @click="goToDetail(item)"/>
            <div class="heart-icon" @click="toggleFavorite(item)">
                <van-icon v-if="item.isFavorite" name="like" color="#fff" />
                <van-icon v-else name="like-o" color="#fff" />
            </div>
          </div>
          <div class="text-shop">
            <p id="shop-p1">{{ item.doller }}</p>
            <p id="shop-p2">{{ item.product }}</p>
          </div>
        </div>
      </div>
</template>
        
    <script>
    export default {
    
        components: {
    
        },
        props: {
            displayedItems: {
                type: Array,
                required: true
            }
    
        },

        data() {
            return {
            };
        },
        computed: {

        },
        methods: {
            goToDetail(item){
                this.$router.push({
                  name:'product_details',
                  params: {
                    src: item.src,
                    doller: item.doller,
                  },
                  query:{
                    product: item.product,
                  },
                })
            },
            toggleFavorite(item) {
                item.isFavorite = !item.isFavorite;
            }
        },
    
    
    
    };
    </script>
    
    <style>
    .waterfall {
      margin:1rem;
      column-count: 2; /* 列数 */
      column-gap: 1rem; /* 列之间的间隙 */
      orphans: 3; /* 保持至少3行文本与图片在同一列 */
      widows: 3;
    
    }
    
    .shop-item {
      margin-bottom: 2rem; /* 项目之间的间隙 */
      break-inside: avoid;
    }
    
    .image {
        border-radius: 20px;
        width: 100%; /* 图片宽度100%填充父容器 */
        break-after: avoid-page;
    }
    .text-shop{
      margin: 1rem 0.2rem;
      text-align: left;
      
    }
    .text-shop p {
      margin: 0rem;
      color: #202043;
    }
    #shop-p1{
      font-size:1.2rem;
    }
    #shop-p2{
      font-size:0.9rem;
    }
    
    .heart-icon{
      border-radius: 42%;
      width: 2rem;
      height: 2rem;
      background-color: rgba(0,0,0,0.1) ;
      position: absolute;
      bottom: 0.5rem; /* 距离底部的距离 */
      right: 0.5rem; /* 距离右侧的距离 */
      display: flex; /* 使用 flex 布局 */
      justify-content: center; /* 水平居中 */
      align-items: center; /* 垂直居中 */
    }
    </style>
    