<template>
    <div class="relative"  style="width:100%; height: 100%; background-color: white;">
    
        <div class="flex-col relative header" >
            <van-icon class="sr-return" name="arrow-left" :size="'30px'" :color="'rgba(51,21,0,0.9)'"
            @click="sr_return"/>
            <div class="sr-bg-tabs" >
                <van-tabs v-model:active="activeTabIndex" type="line">
                        <van-tab title="我要提出建议"></van-tab>
                        <van-tab title="我有全新需求"></van-tab>
                </van-tabs>
            </div>
        </div>
        
        <div class="form-sr-group flex-col justify-center ;" >
            <van-form @submit="onSubmit" class="form-style" style="z-index: 1001;" >
                <van-cell-group inset >
                    <div class="custom-field-wrapper">
                        <van-field class="custom-field"
                        v-model="result" label="类别:" label-align="top" placeholder="请您选择您期望改进的产品分类"
                        is-link readonly  @click="showPicker = true"/>
                        <van-icon class="field-right-icon" name="arrow-down" />
                    </div>
                    
                    <!-- 我的建议 -->
                    <div v-if="activeTabIndex===0" class="custom-field-wrapper">
                        <van-field class="custom-field"
                        v-model="result" label="品牌与产品型号" label-align="top" placeholder="XXXX"
                        is-link readonly  @click="showPicker = true"/>
                        <van-icon class="field-right-icon" name="plus" />
                    </div>
                    <!-- 我的建议 -->

                    <div class="custom-field-wrapper">
                        <van-field class="custom-field"
                        v-model="formData.myQuestion" label="问题描述与期望解决方法" label-align="top" placeholder="问题/期望解决方法"/>
                        <van-icon class="field-right-icon" name="plus" />
                    </div>

                    <!-- 我的需求 -->
                    <div v-if="activeTabIndex===1" class="custom-field-wrapper">
                        <van-field class="custom-field"
                        v-model="result" label="期望产品金额" label-align="top" placeholder="￥35-￥220"
                        is-link readonly  @click="showPicker = true"/>
                        <van-icon class="field-right-icon" name="plus" />
                    </div>
                    <div v-if="activeTabIndex===1" class="custom-field-wrapper">
                        <van-field class="custom-field"
                        v-model="result" label="个人身体尺寸" label-align="top" placeholder="M"
                        is-link readonly  @click="showPicker = true"/>
                        <van-icon class="field-right-icon" name="plus" />
                    </div>
                    <div v-if="activeTabIndex===1" class="custom-field-wrapper">
                        <van-field class="custom-field"
                        v-model="result" label="喜好颜色" label-align="top" placeholder="红 绿"
                        is-link readonly  @click="showPicker = true"/>
                        <van-icon class="field-right-icon" name="plus" />
                    </div>
                    <!-- 我的需求 -->

                    <van-field class="custom-field"
                    v-model="formData.myElse" label-align="top" label="其他说明" />
                </van-cell-group>
            </van-form>
        </div>

        <div class="check-push flex-col justify-start items-start shrink-0">
            <van-checkbox v-model="checked" :checked-color="ringColor">同时分享到交流社区</van-checkbox>
        </div>

        <div v-if="activeTabIndex===0" class="check-push flex-col justify-start items-start shrink-0">
            <van-field
                v-model="value"
                label="留言:"
                placeholder="向大家分享你的心情"
                label-align="top"
            />
        </div>

    <div class="flex-col justify-start items-center self-stretch " >
        <button class="flex-col justify-start items-center success_continue button llll" 
                @click="onClick" :style="{backgroundColor:ringColor}">{{ activeTabIndex === 0 ? '提交建议' : '提交需求' }}</button>
    </div>

        <div  class="relative content--container" >
            <div class="bg-1"></div>
            <div class="bg-2" :style="{ height: bg2Height, backgroundColor:keyColor,opacity:0.65 }"></div>
        </div>
        
    </div>
    </template>
        
    <script>
import {ref, computed } from 'vue';
import { useStore } from 'vuex';
export default {
    setup() {
        const checked = ref(true);

        const store = useStore();
        const switchStatus = computed(() => store.getters['switchStatus']);
        const ringColor = computed(() => switchStatus.value ? '#FF852D' : '#FF2D55');
        const keyColor = computed(() => switchStatus.value ? '#1FAFFF' : '#00C121');
        return {ringColor, keyColor,checked };
    },
        props: {
            bg2Height: {
                type: String, // 或者根据需要改为其他适当的类型，比如 Number
                default: '9.62rem' // 设置默认高度
            },
        },
    
        data() {
            return {
                activeTabIndex: 0,
                formData:{
                    myQuestion:'',
                    myElse:'',
                }
            };
            
        },
        mounted() {
            this.$nextTick(() => {
                this.activeTabIndex = Number(this.$route.query.active);
            });
        },
        computed: {
        },
        methods: {
            sr_return(){
                this.$router.go(-1);
            },

            onSubmit(){
                console.log(this.formData);
            },

            onClick() {
              this.$router.push({ name: 'submit_success' });
            },
        },

    };
    </script>
        
<style>
.llll{
    position: absolute;
    bottom: 1.5rem;
    z-index: 100;
}
.sr-return{
    position: absolute;
    margin-top:4.8rem ;
    margin-left:1rem;
    z-index: 5;
}

.cc{
    position: absolute;
      top: 9.25rem; /* 根据 header 的高度调整 */
      left: 0;
    
      right: 0;
      bottom: 0;
      overflow: auto; /* 如果内容过多，可以滚动 */
}
        .header {
            background-color: rgb(255, 208, 0);
            border-radius: 0rem 0rem 0rem 4rem;
            height: 9.25rem;
        }
    
        .sr-bg-tabs{
            margin:0 1.66rem;
            position: absolute;
            bottom: 2.2rem;
            left:0;
            right:0;
        }
        .sr-bg-tabs .van-tab{
            background:rgb(255, 208, 0);
            font-family: Source Han Sans CN;
            line-height: 1.17rem;
            font-size: 1rem;
            font-weight: bold;
            color:rgba(51,21,0,0.6);    
            padding-bottom: 0.3rem;
        }
        .sr-bg-tabs .van-tab--active{
            color:rgba(51,21,0,1);     
        }
    
        .sr-bg-tabs .van-tabs__line {
            background-color: #000000;
            width:5rem;
    
        }
</style>


<style scoped>
        .bg-1{
            position: fixed;
            bottom: 0;
            background-color: rgba(255, 208, 0, 0.65);
            border-radius: 0rem 4rem 0rem 0rem;
            width: 7.13rem;
            height: 5.81rem;
            z-index: 1;
        }
    
        .bg-2{
            position: fixed;
            bottom: 2.2rem;
            right: 0;
            background-color: rgba(0, 193, 32, 0.65);
            width: 124px;
            border-radius: 4rem 0rem 0rem 4rem;
            z-index: 1;
        }
        @media (min-width: 1025px) {
            .bg-2 {
                right:auto;
                transform: translateX(285px);
            }
        }
    
</style>

<style >
.form-sr-group{
    margin: 1rem 0;
}
/* 改变 label 的颜色和大小 */
.form-style .van-cell__title {
  color: #202043; /* 你想要的颜色 */
  font-size: 16px; /* 你想要的字体大小 */
  letter-spacing: 1px;
}

/* 改变 placeholder 的颜色和大小 */
.form-style .van-field__control::-webkit-input-placeholder {
  color: #B0BFC6; /* 你想要的颜色 */
  font-size: 14px; /* 你想要的字体大小 */
  letter-spacing: 1px;
}

/* 改变输入框右侧的箭头图标为加号 */
.form-style .van-cell__right-icon {
  display: none; /* 隐藏原有的箭头 */
}

.custom-field {
    border-bottom: 1px solid #ddd; /* 分割线的样式 */
}

.custom-field-wrapper{
    position: relative;
}
.field-right-icon{
    position: absolute;
    top:1rem;
    right:1rem;
    font-size: 20px;
}

.check-push{
    margin:0.3rem 2rem;
}

:root{
    --van-checkbox-border-color:#FF2D55;
}

</style>