<template>
<div class="relative"  style="width:100%; height: 100%; background-color: white;">
        <div class="flex-row justify-between items-end message-top">
            <img class="image_m" src="@/assets/view.png" />
            <img class="image_m" src="@/assets/plus.png" />
        </div>

    <div class="flex-col relative header-m" >
        <div class="bg-tabs" >
            <van-tabs v-model:active="currentActive"  type="line"  @change="handleTabChange" default-active="1">
                    <van-tab title="私信"></van-tab>
                    <van-tab title="官方消息"></van-tab>
            </van-tabs>
        </div>
    </div>

    <div v-if="currentActive === 1">
        <div class="flex-row items-center list-item" v-for="(message, index) in office_messages" :key="index" @click="goToMessageDetail(index)">
            <div class="flex-col justify-start items-center shrink-0 image-wrapper-message">
                <img :class="{'shrink-0 message-img-logo':index == 0,'shrink-0 message-img-person':index !== 0}" :src="message.img" />
            </div>
            <div class="flex-col items-start flex-1 group-text-m1">
                <span class="font-m1">{{ message.name }}</span>
                <span class="font-m2 mt-font-m2">{{ message.message1 }}</span>
                <span class="font-m2 ">{{ message.message2 }}</span>
            </div>
        </div>
    </div>

    <div v-if="currentActive === 0">
        <div class="flex-row items-center list-item"  v-for="message in messages" :key="message.name">
            <div class="flex-col justify-start items-center shrink-0 image-wrapper-message">
                <img class="shrink-0 message-img-person" src="@/assets/person/1.png" />
            </div>
            <div class="flex-col items-start flex-1 group-text-m1">
                <span class="font-m1">{{ message.name }}</span>
                <span class="font-m2 mt-font-m2">{{ message.message1 }}</span>
                <span class="font-m2 ">{{ message.message2 }}</span>
            </div>
        </div>
    </div>

    <div  class="relative content--container">
        <div class="bg-1"></div>
        <div class="bg-2" :style="{ height: bg2Height ,backgroundColor: keyColor,opacity:0.65 }"></div>
    </div>

    <img  class="image_mic-move" v-if=false
            src="@/assets/mic.png" />

<div class="flex-col justify-center group-bar">
<ButtomBar :colorchat="'rgb(255,45,85)'"/>
<MicroPhone/> 
</div>
   
</div>
</template>
    
<script>
import MicroPhone from '../MicroPhone.vue'
import ButtomBar from '../ButtomBar.vue';
import { computed } from 'vue';
import { useStore } from 'vuex';
export default {
    setup() {
        const store = useStore();
        const switchStatus = computed(() => store.getters['switchStatus']);
        const keyColor = computed(() => switchStatus.value ? '#1FAFFF' : '#00C121');
        return {keyColor };
    },
    components: {
        ButtomBar,
        MicroPhone
    },
    props: {
        bg2Height: {
            type: String, // 或者根据需要改为其他适当的类型，比如 Number
            default: '9.62rem' // 设置默认高度
        },
    },

    data() {
        return {
            currentActive: 0,
            currentComponent: 'social',
            messages: [
                { name: 'JUNOR', message1: '你好，很高兴认识你~', message2: '我是JUNOR.' },
                { name: 'REUS', message1: '很喜欢你在社区的分享~',message2: '可以认识一下吗？' },
                { name: 'SILVA', message1: '太好了，你也喜欢这本书吗？',message2: '我们可以一起分享' },
            ],
            office_messages: [
                { name: 'VITALITY', message1: '你好，我是vitality', message2: '你的官方助手',img:require('@/assets/office_img.png') },
                { name: 'SANTOS', message1: '已经根据您的需求设计了新的制品呢。', message2: '戳我查看——',img:require('@/assets/person/1.png') },
                { name: 'ROBRET', message1: '您的快递已送达。',message2: '点我预约取件。',img:require('@/assets/person/1.png') },
            ],
        };
        
    },
    mounted() {
        this.$nextTick(() => {
            this.$nextTick(() => {
                this.currentComponent = this.$route.query.active
                ? this.$route.query.active === '购物' ? 'shopping' : this.$route.query.active === '社区' ? 'social' : 'product'
                : 'social';
            });
            // this.activeTabIndex = Number(this.$route.query.active);
        });
    },


    computed: {
    },
    methods: {
        handleTabChange(index) {
            // 处理标签切换事件
            switch (index) {
            case 0:
            this.currentComponent = 'shopping';
            break;
            case 1:
            this.currentComponent = 'social';
            break;
            case 2:
            this.currentComponent = 'product';
            break;
            default:
            this.currentComponent = 'social';
            }
        },
        goToMessageDetail(index) {
            this.$router.push({
                name: 'chat', // 目标页面的路由名称
                query: {
                    messageId: index, // 将需要传递的参数放在 query 中
                },
            });
        },
    },



};
</script>
        
<style>
    .header-m {
        background-color: rgb(255, 208, 0);
        border-radius: 0rem 0rem 0rem 4rem;
        height: 5.25rem;
    }

    .message-top{
        background-color: rgb(255, 208, 0);
        padding:2.5rem 1.5rem 0;
    }
    .image_m{
        width: 1.88rem;
        height: 1.88rem;
    }

    .bg-tabs{
        margin:0 1.66rem;
        position: absolute;
        bottom: 2.2rem;
        left:0;
        right:0;
    }
    .bg-tabs .van-tab{
        background:rgb(255, 208, 0);
        font-family: Source Han Sans CN;
        line-height: 1.17rem;
        font-size: 1rem;
        font-weight: bold;
        color:rgba(51,21,0,0.6);    
        padding-bottom: 0.3rem;
    }
    .bg-tabs .van-tab--active{
        color:rgba(51,21,0,1);     
    }

    .bg-tabs .van-tabs__line {
        background-color: #000000;
        width:3rem;

    }

    .bg-1{
        position: fixed;
        bottom: 0;
        background-color: rgba(255, 208, 0, 0.65);
        border-radius: 0rem 4rem 0rem 0rem;
        width: 7.13rem;
        height: 5.81rem;
        z-index: 1;
    }

    .bg-2{
        position: fixed;
        bottom: 2.2rem;
        right: 0;
        background-color: rgba(0, 193, 32, 0.65);
        width: 124px;
        border-radius: 4rem 0rem 0rem 4rem;
        z-index: 1;
    }
    @media (min-width: 1025px) {
        .bg-2 {
            right:auto;
            transform: translateX(285px);
        }
    }


    .image_mic-move {
        position: absolute;
        width: 4.38rem;
        height: 4.38rem;
    }

    .content--container{
        top:0;
        bottom: 0;
        right: 0;
        left: 0;
        overflow: auto; /* 如果内容过多，可以滚动 */
    }
</style>
    
<style>
.list-item {
  padding-left: 0.94rem;
  margin-top: 0.3rem;
  padding-top: 0.2rem;
}

.image-wrapper-message {
  padding-top: 0.26rem;
  width: 4.38rem;
  height: 4.38rem;
}
.message-img-person {
  background-color: rgb(197, 236, 253);
  border-radius: 62.44rem;
  overflow: hidden;
  width: 4.38rem;
  height: 4.13rem;
}

.group-text-m1{
    padding: 0.94rem 0 1.03rem;
    border-bottom: solid 0.035rem rgba(0, 0, 0, 0.3);
    margin-left: 1.06rem;
}
.font-m1{
    font-size: 0.88rem;
    font-family: Source Han Sans CN;
    line-height: 0.72rem;
    font-weight: 700;
    color: rgb(0, 0, 0);
    font-size: 0.94rem;
}
.font-m2{
    font-size: 0.88rem;
    font-family: Source Han Sans CN;
    line-height: 1.13rem;
    color: rgb(166, 166, 166);
    margin-top: 0.3rem;
}
.mt-font-m2{
    margin-top: 0.8rem;
}

.message-img-logo{
    flex-shrink: 0;
    width: 4.38rem;
    height: 4.38rem;
}
</style>