import { createApp } from 'vue';
import App from './App.vue'
import router from './router/index.js';
import store from './store';
import { library } from '@fortawesome/fontawesome-svg-core'
import { faMagnifyingGlass } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'
import { Tab,Tabs,Icon ,Tag,Cell  } from 'vant';
import { Tabbar, TabbarItem ,Button,Switch } from 'vant';
import { Form, Field, CellGroup } from 'vant';
import { Checkbox, CheckboxGroup ,NavBar} from 'vant';
import { RadioGroup, Radio } from 'vant';
import { Step, Steps,Divider } from 'vant';
import { Sidebar, SidebarItem } from 'vant';


const app = createApp(App);
app.use(router); // 使用路由器
app.use(store);
app.mount('#app');
app.use(Tab).use(Tabs).use(Icon ).use(Tag).use(Cell).use(RadioGroup).use(Radio);
app.use(CellGroup).use(Field).use(Form).use(Checkbox).use(CheckboxGroup);
app.use(Tabbar).use(TabbarItem).use(Button).use(Switch).use(NavBar);
app.use(Step).use(Steps).use(Divider).use(Sidebar).use(SidebarItem)
app.component('font-awesome-icon', FontAwesomeIcon)
library.add(faMagnifyingGlass)
// 获取屏幕高度
var screenHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;

// 设置根元素的字体大小（假设基准高度为 800px，基准字体大小为 16px）
var baseFontSize = 16;
var baseHeight = 800;
var rootFontSize = (screenHeight / baseHeight) * baseFontSize;
document.documentElement.style.fontSize = rootFontSize + 'px';

// 监听窗口大小改变事件，动态更新根元素的字体大小
window.addEventListener('resize', function() {
  screenHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
  rootFontSize = (screenHeight / baseHeight) * baseFontSize;
  document.documentElement.style.fontSize = rootFontSize + 'px';
});
