<template>
    <div class="relative"  style="width:100%; height: 100%; background-color: rgb(252,252,252);">
        <van-nav-bar title="身体情况填写" class="about-title"/>
        
        <div class="about-group">
            <div class="about-1-title flex-row">
                <span id="text-about">残疾类型选择</span>
                <span id="text-about-x">(可多选)</span>
            </div>

            <div class="button-container">
                <div v-for="(item, index) in buttons1" :key="index" class="button-row">
                    <button class="about-1-button" @click="toggleFollowState(index)" :style="{ backgroundColor: item.bgColor }">
                    {{ item.label }}
                    </button>
                </div>
            </div>


            <div class="about-1-title flex-row">
                <span id="text-about">残疾等级选择</span>
            </div>

            <div class="button-container">
                <div v-for="(item, index) in buttons2" :key="index" class="button-row">
                    <button class="about-2-button" @click="toggleOne(index)" :style="{ backgroundColor: item.bgColor }">
                    {{ item.label }}
                    </button>
                </div>
            </div>
        </div>
        
        <div  class="relative content--container">
            <div class="bg-1"></div>
            <div class="bg-2" :style="{ height: bg2Height }"></div>
        </div>
        <div class="flex-col justify-start items-center self-stretch botton-about">
            <div class="flex-col justify-start items-center botton-about-yes" @click="onClick"><span class="text-about">确 认</span></div>
         </div>
        
</div>
</template>
    
<script>

export default {

    components: {

    },
    props: {
        bg2Height: {
            type: String, // 或者根据需要改为其他适当的类型，比如 Number
            default: '9.62rem' // 设置默认高度
        },
    },

    data() {
        return {
            buttons1:[
                {label:"上肢残疾人士",bgColor:'rgba(255, 209, 0, 0.8)'}, 
                {label:"下肢残疾人士",bgColor:'rgba(255, 209, 0, 0.3)'},
                {label:"听障人士"    ,bgColor:'rgba(255, 209, 0, 0.3)'}, 
                {label:"视障人士"    ,bgColor:'rgba(255, 209, 0, 0.3)'},
                {label:"言语残疾人士",bgColor:'rgba(255, 209, 0, 0.3)'}, 
                {label:"其他"        ,bgColor:'rgba(255, 209, 0, 0.3)'},
            ],
            buttons2:[
                {label:"一级",bgColor:'rgba(255, 209, 0, 0.3)',selected: false }, 
                {label:"二级",bgColor:'rgba(255, 209, 0, 0.3)',selected: false },
                {label:"三级",bgColor:'rgba(255, 209, 0, 0.3)',selected: false }, 
                {label:"四级",bgColor:'rgba(255, 209, 0, 0.3)',selected: false },
            ],
        };
        
    },
    mounted() {
    },


    computed: {
    },
    methods: {
        onClick() {
            this.$router.push({ name: 'recommend' });
        },
        toggleFollowState(index) {
            // 确保 index 在 buttons1 数组的索引范围内
            if (this.buttons1 && index >= 0 && index < this.buttons1.length) {
                this.buttons1[index].bgColor = this.buttons1[index].bgColor === 'rgba(255, 209, 0, 0.8)' ? 'rgba(255, 209, 0, 0.3)' : 'rgba(255, 209, 0, 0.8)';
            } else {
                console.error('Invalid index:', index);
            }
        },
        toggleOne(index) {
            // 取消选中其他按钮
            this.buttons2.forEach((button, i) => {
                if (i !== index) {
                button.selected = false;
                button.bgColor = 'rgba(255, 209, 0, 0.3)';
                }
            });

            // 切换选中状态
            this.buttons2[index].selected = !this.buttons2[index].selected;

            // 切换按钮背景颜色
            this.buttons2[index].bgColor = this.buttons2[index].selected ? 'rgba(255, 209, 0, 0.8)' : 'rgba(255, 209, 0, 0.3)';
            }
        },
};
</script>
        
<style>
.about-title{
    padding:2rem 0 0rem;
    border-bottom: 1px solid #EAEDF2;
}


    .bg-1{
        position: fixed;
        bottom: 0;
        background-color: rgba(255, 208, 0, 0.65);
        border-radius: 0rem 4rem 0rem 0rem;
        width: 7.13rem;
        height: 5.81rem;
        z-index: 1;
    }

    .bg-2{
        position: fixed;
        bottom: 2.2rem;
        right: 0;
        background-color: rgba(0, 193, 32, 0.65);
        width: 124px;
        border-radius: 4rem 0rem 0rem 4rem;
        z-index: 1;
    }
    @media (min-width: 1025px) {
        .bg-2 {
            right:auto;
            transform: translateX(285px);
        }
    }
    .about-group{
        margin:1rem 2rem;
        color:#331500;
        font-size: 0.9rem;
        
    }
    #text-about{
        letter-spacing: 2px;
        font-weight: bold;
        font-size:1.1rem;
    }
    #text-about-x{
        color:#A6A6A6;
        font-size:0.9rem;
        margin-left:15px;
        letter-spacing: 1px;
    }

    .botton-about{
        position: absolute;
        bottom:3.5rem;
        z-index:100;
        left:50%;
        transform: translateX(-50%);
    }

    .button-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: start;
        margin:1rem 0;
    }

    .button-row {
        display: flex;
        margin-right:0.5rem;
        margin-bottom: 1rem; /* 按钮行之间的间距 */
    }
    .about-1-button{
        border-radius: 0.94rem;
        padding: 0.45rem 1.5rem;
        margin-right: 0.6rem;
        border: none;
        letter-spacing: 0.1rem;
    }
    .about-2-button{
        border-radius: 0.94rem;
        border-radius: 2rem;
        padding: 0.8rem 3rem;
        margin-right: 1rem;
        border: none;
        letter-spacing: 0.1rem;
    }


    .botton-about-yes {
        padding: 1.02rem 1.5rem;
        background-color: rgb(255, 45, 85);
        border-radius: 2rem;
        width: 13.69rem;
        font-size:16px;
        margin: 0 5px;
    }
    .text-about {
        color: rgb(255, 255, 255);
        font-size: 1rem;
        font-family: Source Han Sans CN;
        line-height: 1.15rem;
    }

</style>
