<template>
    <div class="pp"  ref="draggable" @mousedown="onMouseDown" @mousemove="onMouseMove" @mouseup="onMouseUp">
        <div class="mic-set-bg" @click="onclick" :style="{backgroundColor:keyColor}"></div>
    </div>
</template>
  
<script>
import { computed } from 'vue';
import { useStore } from 'vuex';
export default {
    setup() {
        const store = useStore();
        const switchStatus = computed(() => store.getters['switchStatus']);
        const keyColor = computed(() => switchStatus.value ? '#1FAFFF' : '#00C121');
        return {keyColor };
    },
  data() {
    return {
      isDragging: false, // 是否正在拖拽
      startY: 0, // 鼠标按下时的初始位置
      offsetTop: 0, // 元素距离顶部的偏移量
    };
  },
  computed: {

  },

  mounted() {
    document.addEventListener('mousemove', this.onMouseMove);
    document.addEventListener('mouseup', this.onMouseUp);
  },
  beforeUnmount() {
    document.removeEventListener('mousemove', this.onMouseMove);
    document.removeEventListener('mouseup', this.onMouseUp);
  },
  methods: {
    onclick(){
      this.$router.push({name:'talkAi'})
    },

    onMouseDown(event) {
      this.isDragging = true;
      this.startY = event.clientY;
      this.offsetTop = this.$refs.draggable.offsetTop;
    },
    onMouseMove(event) {
      if (this.isDragging) {
        const deltaY = event.clientY - this.startY;
        const newTop = this.offsetTop + deltaY;
        const maxHeight = window.innerHeight - 10; // 上边界
        const minHeight = 80; // 下边界
        // 限制拖拽范围
        const constrainedTop = Math.max(Math.min(newTop, maxHeight), minHeight);
        this.$refs.draggable.style.top = `${constrainedTop}px`;
      }
    },
    onMouseUp() {
      this.isDragging = false;
    },
  },
  
  
};
</script>
<style scoped>
.pp{
  position: absolute;
  bottom: 3.5rem;
  z-index: 1000;
}

.mic-set-bg{

  border-radius: 50%;
  height: 4.38rem;

  background-image: url("@/assets/mic-none.png");
  box-shadow: 0 5px 6px rgba(0, 0, 0, 0.3);
  background-size:50%; /* 背景图片覆盖整个元素 */
  background-position: center;
  background-repeat: no-repeat;

  position: fixed;
  bottom: 4rem; /* 距离底部 20px */
  width: 60px; /* 宽度 */
  height: 60px; /* 高度 */
  cursor: pointer; 
  z-index: 10;
}
</style>