<script>
import MicroPhone from '../MicroPhone.vue'
import ButtomBar from '../ButtomBar.vue';
import {ref, watch,computed} from 'vue';
import { useStore } from 'vuex';

export default ({
    components: {
        ButtomBar,
        MicroPhone
    },
    setup() {
        //const checked1 = ref(false);
        const store = useStore();
        const checked2 = ref(store.getters['switchStatus']);
        watch(checked2, (newValue) => {
            store.commit('SET_SWITCH', newValue);
        });
        const checked1 = computed({
            get:()=>store.getters['blockVisible'],
            set: (value) => store.commit('SET_BLOCK_VISIBLE', value)
        })
        const ringColor = computed(() => checked2.value ? '#FF852D' : '#FF2D55');
        return { checked1,checked2,ringColor };
    },
    methods: {

    }
})
</script>

