
import  { createRouter, createWebHashHistory }   from 'vue-router';
import Login_1 from '../components/login/Login_1.vue';
import Login_2 from '../components/login/Login_2.vue';
import Login_3 from '../components/login/Login_3.vue';
import Login_4 from '../components/login/Login_4.vue';
import aboutyou from '../components/login/AboutYou.vue';
import recommend from '../components/login/ToRecommend.vue';
import product_details from '../components/login/ProductDetails.vue';


import index_user from '../components/index_user/index_user.vue';
import submit_success from '../components/index_user/Submit_Success.vue';
import support from '../components/index_user/ToSupport.vue';
import classifyDetail from '../components/index_user/shop/ClassifyDetail.vue';

import share from '../components/index_user/social/social_share.vue';
import hisIndex from '../components/index_user/social/his_index.vue';

import suggest_require from '../components/index_user/product-make/suggest_require.vue';

import my from '../components/my-user/MySelf.vue';
import mydata from '../components/my-user/MyData.vue';
import myproduct from '../components/my-user/MyProduct.vue';

import message from '../components/message/MessagePage.vue';
import chat from '../components/message/ChatPage.vue';

import TalkAi from '../components/TalkAi.vue';

const routes = [
    {
      path: '/',
      name: 'login_01',
      component: Login_1,
    },
    {
      path: '/login_02', // 添加路径
      name: 'login_02',
      component: Login_2,
    },
    {
      path: '/login_03',
      name: 'login_03',
      component: Login_3,
    },
    {
      path: '/login_04',
      name: 'login_04',
      component: Login_4,
    },
    {
      path: '/aboutyou',
      name: 'aboutyou',
      component: aboutyou,
    },
    {
      path: '/aboutyou/recommend',
      name: 'recommend',
      component: recommend,
    },
    {
      path: '/aboutyou/shop/details/:src/:doller',
      name: 'product_details',
      component: product_details,
    },
    {
      path: '/submit_success',
      name: 'submit_success',
      component: submit_success,
    },
    {
      path: '/social/share',
      name: 'share',
      component: share,
    },
    {
      path: '/talkAi',
      name: 'talkAi',
      component: TalkAi,
    },
    {
      path: '/user/index',
      name: 'index_user',
      component: index_user,
    },
    {
      path: '/user/index/support',
      name: 'support',
      component: support,
    },
    {
      path: '/user/hisIndex',
      name: 'hisIndex',
      component: hisIndex,
    },
    {
      path: '/user/product/suggestANDrequire',
      name: 'suggest_require',
      component: suggest_require,
    },
    {
      path: '/user/shop/classifyDetail',
      name: 'classifyDetail',
      component: classifyDetail,
    },

    {
      path: '/user/my',
      name: 'my',
      component: my,
    },
    {
      path: '/user/my/myproduct',
      name: 'myproduct',
      component: myproduct,
    },
    {
      path: '/user/my/mydata',
      name: 'mydata',
      component: mydata,
    },
    {
      path: '/user/message',
      name: 'message',
      component: message,
    },
    {
      path: '/user/message/chat',
      name: 'chat',
      component: chat,
    },
];
  

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
});

export default router;
