// store/modules/switch.js
export default {
    state: {
        colorBlindMode: false,
        singleMode: false
        
    },
    mutations: {
        SET_SWITCH(state, value) {
            state.colorBlindMode = value;
        },
        SET_BLOCK_VISIBLE(state, value) {
            state.singleMode = value;
        },
    },
    actions: {
        TOGGLE_SWITCH({ commit, state }) {
            const newValue = !state.colorBlindMode;
            commit('SET_SWITCH', newValue);
        }
    },
    getters: {
      switchStatus: state => state.colorBlindMode,
      blockVisible: state => state.singleMode,
    }
  };
  