<template>
<div class="relative"  style="width:100%; height: 100%; background-color: white;">
    <div class="flex-col relative header" >
        <div class="bg-tabs" >
            <van-tabs v-model:active="currentActive"  type="line"  @change="handleTabChange" default-active="1">
                    <van-tab title="产品商城"></van-tab>
                    <van-tab title="交流社区"></van-tab>
                    <van-tab title="产品定制"></van-tab>
            </van-tabs>
        </div>
    </div>
    <div class="cc" style="z-index: 2;">
        <component :is="currentComponent" @switch-tab="switchToTab1" :colors="[ringColor, keyColor]"/>
    </div>

    <div  class="relative content--container">
        <div class="bg-1" ></div>
        <div class="bg-2" :style="{ height: bg2Height ,backgroundColor: keyColor,opacity:0.65}"></div>
    </div>

    <img  class="image_mic-move" v-if=false
         src="@/assets/mic.png" />

    <div class="flex-col justify-center group-bar">
    <ButtomBar :colorhome="'rgb(255,45,85)'"/>
    <MicroPhone/>
</div>
    
</div>
</template>
    
<script>
import MicroPhone from '../MicroPhone.vue'
import product from './product-make/product_make.vue';
import shopping from './shop/secondhand_mall.vue';
import social from './social/social_community.vue';
import ButtomBar from '../ButtomBar.vue';
import { computed } from 'vue';
import { useStore} from 'vuex';
export default {
    setup() {
        const store = useStore();
        const switchStatus = computed(() => store.getters['switchStatus']);
        const ringColor = computed(() => switchStatus.value ? '#FF852D' : '#FF2D55');
        const keyColor = computed(() => switchStatus.value ? '#1FAFFF' : '#00C121');
        return {ringColor, keyColor };
    },

    components: {
        product,
        shopping,
        social,
        ButtomBar,
        MicroPhone
    },
    props: {
        bg2Height: {
            type: String, // 或者根据需要改为其他适当的类型，比如 Number
            default: '15.62rem' // 设置默认高度
        },
    },

    data() {
        return {
            currentActive: 1,
            currentComponent: 'social',
        };
        
    },
    mounted() {
        this.$nextTick(() => {
            this.$nextTick(() => {
                this.currentComponent = this.$route.query.active
                ? this.$route.query.active === '购物' ? 'shopping' : this.$route.query.active === '社区' ? 'social' : 'product'
                : 'social';
            });
        });
    },
    methods: {
        handleTabChange(index) {
            // 处理标签切换事件
            switch (index) {
            case 0:
            this.currentComponent = 'shopping';
            break;
            case 1:
            this.currentComponent = 'social';
            break;
            case 2:
            this.currentComponent = 'product';
            break;
            default:
            this.currentComponent = 'social';
            }
        },
        switchToTab1() {
            // 切换到标签1
            this.currentActive = shopping; // 0 是标签1的索引
        }
    },
};
</script>
    
<style>
.ccc{
    position: absolute;
    top: 9.25rem; /* 根据 header 的高度调整 */
    left: 0;

    right: 0;
    bottom: 0;
    
}
    .header {
        background-color: rgb(255, 208, 0);
        border-radius: 0rem 0rem 0rem 4rem;
        height: 9.25rem;
    }

    .bg-tabs{
        margin:0 1.66rem;
        position: absolute;
        bottom: 2.2rem;
        left:0;
        right:0;
    }
    .bg-tabs .van-tab{
        background:rgb(255, 208, 0);
        font-family: Source Han Sans CN;
        line-height: 1.17rem;
        font-size: 1rem;
        font-weight: bold;
        color:rgba(51,21,0,0.6);    
        padding-bottom: 0.3rem;
    }
    .bg-tabs .van-tab--active{
        color:rgba(51,21,0,1);     
    }

    .bg-tabs .van-tabs__line {
        background-color: #000000;
        width:3rem;

    }

    .bg-1{
        position: fixed;
        bottom: 0;
        background-color: rgba(255, 208, 0, 0.65);
        border-radius: 0rem 4rem 0rem 0rem;
        width: 7.13rem;
        height: 5.81rem;
        z-index: 1;
    }

    .bg-2{
        position: fixed;
        bottom: 2.2rem;
        right: 0;
        /* background-color: rgba(0, 193, 32, 0.65); */
        width: 124px;
        border-radius: 4rem 0rem 0rem 4rem;
        z-index: 1;
    }
    @media (min-width: 1025px) {
        .bg-2 {
            right:auto;
            transform: translateX(285px);
        }
    }


    .image_mic-move {
        position: absolute;
        width: 4.38rem;
        height: 4.38rem;
    }

    .content--container{
        top:0;
        bottom: 0;
        right: 0;
        left: 0;
        overflow: auto; /* 如果内容过多，可以滚动 */
    }
</style>

