<template>
    <div class="topbar relative">
        <van-nav-bar title="我的产品" left-arrow @click-left="onClickLeft"/>
        <div class="step-group">
            <van-steps :active="StepActive" active-color="rgb(0, 193, 32)">
                <van-step>已发起</van-step>
                <van-step>设计中</van-step>
                <van-step>制作中/已完成</van-step>
            </van-steps>
        </div>
    </div>

    <div v-if="StepActive === 0" class="comp1 flex-col">
        <van-field v-model="formValue1.value1" 
        label="问题"  label-align="top" class="comp1-label"/>
        <van-field v-model="formValue1.value2" 
        label="期望解决办法" label-align="top" class="comp1-label"/>
        <van-cell title="需求描述主页" is-link class="comp1-label"/>
        <van-cell title="发起时间" value="2023.1.25" class="comp1-label"/>
    </div>

    <div v-if="StepActive === 1" class="comp1 flex-col">
        <van-field v-model="formValue2.value1" 
        label="需求" placeholder="适合六指打字的键盘"  label-align="top" class="comp1-label"/>
        <div class="flex-row  justify-between items-center">
            <van-field v-model="formValue2.value2" 
            label="产品" placeholder="六指分体式键盘"  label-align="top" class="comp1-label comp2-label1" />
            <van-field v-model="formValue2.value3" 
            label="接单设计师" placeholder="李红"  label-align="top" class="comp1-label comp2-label2"/>
        </div>
        <van-cell title="设计师主页" is-link class="comp1-label"/>
        <van-cell title="接单时间" value="2023.3.10" class="comp1-label"/>
        <van-cell title="查看设计" is-link class="comp1-label"/>
        <div class="comp-button">
            <van-button  round type="primary" >填写反馈</van-button>
        </div>
    </div>

    <div v-if="StepActive === 2" class="comp1 flex-col">
        <van-field v-model="formValue3.value1" 
        label="产品" placeholder="六指分体式键盘"  label-align="top" class="comp1-label"/>
        <div class="flex-row  justify-between items-center">
            <van-field v-model="formValue3.value2" 
            label="厂家" placeholder="北京红星科技厂"  label-align="top" class="comp1-label comp2-label1"/>
            <van-field v-model="formValue3.value3" 
            label="接单设计师" placeholder="李红"  label-align="top" class="comp1-label comp2-label2"/>
        </div>
        <van-cell title="厂家介绍" is-link class="comp1-label"/>
        <van-cell title="产品细节" is-link class="comp1-label"/>
        <van-cell title="接单时间" value="￥25" class="comp1-label"/>
        <div class="comp-button">
            <van-button  round type="primary" >立即购买</van-button>
        </div>
    </div>
  

    <div class="stepbutton">
        <van-button  round type="primary" @click="onNextStep">{{ buttonText }}</van-button>
    </div>
  </template>
  
  <script>
import { ref } from 'vue';
export default {
    components(){

    },
      setup() {
            const StepActive = ref(0);
            const buttonText = ref('下一步');
            const onClickLeft = () => {
                if(StepActive.value === 0){
                    history.back();
                } else{
                    StepActive.value--;
                    if(StepActive.value === 1){
                        buttonText.value = '下一步';
                    }
                }
            }
            const onNextStep = () => {
                if (StepActive.value < 2) {
                    StepActive.value++;
                    if(StepActive.value === 2){
                        buttonText.value = '返  回';
                    }
                } else{
                    
                    history.back();
                }
            };
            return {
                onClickLeft,
                StepActive,
                onNextStep,
                buttonText,
            };
      },
      data(){
        return {
            formValue1: {
                value1: '市面上键盘六指打字慢',
                value2: '为六指群体设计符合需求的专用键盘',
            },
            formValue2: {
                value1: '适合六指打字的键盘',
                value2: '六指分体式键盘',
                value3: '李红',
            },
            formValue3: {
                value1: '六指分体式键盘',
                value2: '北京红星科技厂',
                value3: '李红',
            },
        }
    }
 }
  </script>
  
  <style>
  .topbar{
      padding-top:1.5rem;
      background-color: white;
      padding-bottom: 4.5rem;
      border-bottom: 1px solid rgba(184, 183, 183, 0.36);
  }

  :root{
      --van-nav-bar-height:5rem;
      --van-nav-bar-arrow-size:20px;
      --van-nav-bar-icon-color:#202043;
      --van-nav-bar-title-font-size:18px;
      --van-nav-bar-title-text-color:#43484B;
      --van-step-icon-size:18px;
      --van-step-circle-size:16px;
      --van-step-circle-color:rgb(209, 206, 206);
      --van-step-line-color:#00C121;
      --van-step-finish-line-color:#00C121;
      --van-step-text-color: #43484B;

  }

    .comp1-label .van-field__label {
        color: #A6ABC4; /* 自定义颜色 */
        font-size: 13px; /* 自定义字体大小 */
    }
    .comp1-label .van-cell__title {
        color: #A6ABC4; /* 自定义颜色 */
        font-size: 13px; /* 自定义字体大小 */
    }
    .comp1-label .van-field__control {
        box-sizing: border-box;
        border-bottom: 1px solid #d7d8dd;
        padding: 3px;
    }
    .comp2-label1{
        margin-right: 0.2rem;
        flex-grow: 1;
    }
    .comp2-label2{
        width:60%;
        margin-left: 0.2rem;
    }


  .step-group{
    width: 70%;
    position: absolute;
    top: 6.5rem;
    left:50%;
    transform: translateX(-50%);
  }

  .comp-button{
    display: flex;
    justify-self: center;
    align-self: center;
  }
  .comp-button .van-button{
    padding:1rem 5rem;
    margin: 1rem 0;
    background-color: #FF2D55;
    border: none;
  }

  .stepbutton{
    position: absolute;
    bottom: 1.5rem;
    left:50%;
    transform: translateX(-50%);
    padding: 1rem 0 1rem;

  }

  .stepbutton .van-button{
    padding:1.8rem 5rem;
    margin: 1rem 0;
    background-color: #FF2D55;
    border: none;
    white-space: nowrap;
  }

  .comp1{
    margin: 2rem 3rem;
    padding: 1rem 1.5rem;
    background-color: rgb(255, 255, 255);
    border-radius: 0.5rem;
    box-shadow: 0rem 0.31rem 1rem rgba(0, 0, 0, 0.09);

  }
  </style>